export default {"name":"沱茶","img":"\\heicha\\tuocha\\img_20191122132333fo62pk.jpg","desc":["沱茶是一种制成圆锥窝头状的紧压茶，主要产地是云南，一般用黑茶制造，为便于马帮运输，一般将几个用油纸包好的茶坨连起，外包稻草做成长条的草把。因为一个茶坨的分量比一块茶砖要小得多，更容易购买和零售。沱茶从面上看似圆面包，从底下看似厚壁碗，中间下凹，颇具特色。沱茶的种类，依原料不同有绿茶沱茶和黑茶沱茶之分。"],"menus":[{"name":"沱茶茶叶历史","content":[{"text":"沱茶的产制历史悠久，早在明代谢肇制的《滇略》一书中有“士庶用皆普茶也，蒸而团之”的记载。普茶指普洱茶，说明当时已有将散茶蒸后，加工揉制，压缩体积，便于携带的压制茶了。历史上，景谷人李文相于光绪二十六年(1900)创办制茶作坊，用晒青毛茶作原料土法蒸压月饼形团茶，又名谷茶。两年后被下关“茂恒”、“永昌祥”商家仿制成“碗形茶”，经昆明、昭通运到四川叙府(今宜宾)、沱江一带销售。景谷团茶的加工，奠定了云南沱茶的雏形。沱茶名称的由来，沱是由团转化而来，有说由于过去运销四川沱江一带，故而得名。不论说法如何，但可以推定，沱茶是云南茶中相当古老的制品，早就畅销省内外。现代形状的云南沱茶创制于1902(清光绪二十八年年)，已有八十多年的历史，是由思茅地区景谷县所谓“姑娘茶”(又叫私房茶)演变而成现代沱茶的形状。清代末叶，云南茶叶集散市场逐渐转移到交通方便、工商业发达的下关。下关永昌祥、复春和等茶商改团茶制成碗状形沱茶，经昆明运往重庆、叙府(今宜宾)、成都等地销售，故又称叙府茶。"}]},{"name":"沱茶茶叶特点","content":[{"text":"沱茶外观显毫，品质最好的是“特级沱茶”，香气馥郁，滋味醇厚，喉味回甘，汤色橙黄明亮。普洱散茶蒸压而成的云南普洱沱茶，外形紧结，色泽裼红，有独特的陈香，滋味回甘，汤色橙黄明亮。"},{"src":"\\heicha\\tuocha\\img_20191122132333lnncdq.jpg"}]},{"name":"沱茶加工工艺","content":[{"text":"一、原料选择："},{"text":"沱茶特殊的色、香、味与原料密切相关，沱茶品质风格，不仅与毛茶品种、产地、制法密切相关，相互间的搭配更是提高品质的关键之一。"},{"text":"下关沱茶都是用云南大叶种晒青毛茶压制的，重庆沱茶：根据外形，内质确定比例，做到“四个结合”：即春夏秋茶结合;高山丘陵、低地茶相结合;大叶种与中、小叶种相结合;烘青、炒青与晒青相结合。"},{"text":"毛茶进厂后，经过验收复评，定级归堆，进行毛茶加工，程序与一般\n        绿茶\n        相同(筛分、风选、拣剔等)，只是毛茶规格较肥大。紧门筛为5-6孔(配置一定规格的筛网进行复抖，又称规格筛)。割脚筛为24孔，筛分的三、四口茶，经7孔和5孔抖筛。抖底拼作盖茶，抖面和其它各口拼为底茶，除去碎片、末、梗子及茶灰，然后复火，使水分保持在12%左右。"},{"text":"二、称茶与蒸茶："},{"text":"各种沱茶不同重量的规格有三种(50g、100g、250g)，按下式计算出每次的称量："},{"text":"称茶重量=成品标准重量×成品标准干度"},{"text":"原料实际干度(1-加工损耗率)"},{"text":"例如：成品标准重量为250g，标准干度为91%，半成品干度为88%，加工损耗率为1%，则称茶重量为261.2g。"},{"text":"每次称量后，倒入底部有许多小孔的圆形蒸茶筒内，通过蒸汽，蒸10-20s到茶叶及湿变软，含水量增加3%时为好。"},{"text":"蒸茶太久，茶叶会水过多，造成茶叶黄熟，香气降低，引起霉变。如蒸时太短，蒸汽不足，叶质不易变软，成型较为困难，即容易产生脱层现象。"},{"text":"三、揉袋施压："},{"text":"将蒸好的茶叶，倒入三角形圆底小布袋，左手捏紧袋口，右手拿茶，在作业台上轻轻揉转几下，将口袋置于茶团中央，袋底朝上，然后用圆柱形小木棒顶住，取出木棒，将茶袋放于碗形钢模上，用杠杆人力加压成形。"},{"text":"四、定型干燥："},{"text":"压制好的沱茶，需经过30min的摊放冷却、定型，待茶团热气散失后，才可脱袋，脱袋后及时用高标纸把茶叶包好，放于烘盘，准备干燥。"}]},{"name":"沱茶储藏方法","content":[{"text":"茶叶中的一些成分不稳定，在一定的物理、化学诱因下，易产生化学变化，就是通常所说的茶变。茶叶的保存条件不好会加速茶叶的自身氧化及霉变。"},{"src":"\\heicha\\tuocha\\img_20191122132333m920u4.jpg"},{"text":"保存茶叶的容器以锡瓶、瓷坛、有色玻璃瓶为最佳。其次宜用铁罐、木盒、竹盒等，其中竹盒不宜在干燥的北方使用。塑料袋、纸盒最次。保存茶叶的容器要干燥、洁净、不得有异味。"},{"text":"盛好保存的茶叶，宜放在干燥通风无异味之处。不能放在潮湿、高温、不洁、曝晒的地方。且周围不能有樟脑、药品、化妆品、香烟、洗涤用品等有强烈气味的物品。还要注意，不同种类、不同级别的茶叶不能混在一起保存。不能在保存\n        红茶\n        、\n        花茶\n        时使用生石灰作吸湿剂。"}]},{"name":"沱茶冲泡方法","content":[{"text":"选壶"},{"text":"壶的选用最好是续温力强的、稍大(比喝乌龙时大约两三倍)，会比较好些。原因是在第一、二冲之后的冲泡，应尽量在泡与泡之间进行，所以茶壶续温力不佳容易使温度造成很大差异。所以茶壶选择壁厚、壶盖口比例不要太大的。另外值得一提的是，茶壶的出水最好流畅一点，有时置茶量太多，沸水冲入壶中需要马上倒出，所以出水不够快的壶会影响执壶者的操控能力。"},{"src":"\\heicha\\tuocha\\img_20191122132333anlslg.jpg"},{"text":"壶的表现"},{"text":"前项中既然谈到了壶，就顺便说说壶的好处。泡普洱一般使用壶、盖杯或茶娘(或瓷壶)三种，只有壶不是瓷器。香气细嫩、韵味柔韧、涩度不高、酸度不高、苦味不重者，建议使用瓷器(例如嫩沱、或称为云尖之类的)，但是因为续温力问题，所以与壶冲泡出来的口感有差异。一般的老普洱，如果用壶泡，通常会减少一些让人不愉悦的感觉，例如涩、苦、火气、单宁、酸…等等。如果一支常泡的新壶，大约半年就可以养成了。养成的壶冲泡起来会比较好喝。当然，四、五年以上的普洱壶(部分物质会减少)与盖杯(茶叶的东西全部跑出来)的差异更大。"},{"text":"壶内留汤的特效"},{"text":"一般来说，第一泡水倒干，第二泡壶中的水留一些，大约十分之一(这是在第二泡将香味冲出来的状况，如果第三泡才能将香味冲出来，就在第三泡才开始在壶中留一点水，不要倒干)。如果真的够老的普洱，不需开盖，严格说应该是不能开盖;会有些许涩味、酸味等的普洱，在第二泡倒出并"},{"text":"冲泡后的沱茶在壶中留点水之后，开盖大约半分钟到一分钟再把盖子盖上;比较重的涩味、苦味、酸味等的普洱，开盖，壶口没有蒸汽时，用手指压壶身，若温度不会太烫手指可以摸五秒以上，再将壶盖盖上。壶中留点水，主要让冲泡出的东西溶解在水中;温度要控制，因为这是主要的冲泡。停壶时间就是冲泡(浸泡、浸茶)的时间，所以，这段时间要控制好(如果将温度设定的比较高就需要战战兢兢)，茶汤才会显现出该有的风味。所以，除非置茶量太多、温度太高，否则不应立即冲下一泡。"},{"text":"适时出汤"},{"text":"以第二泡冲出香味为例，第三泡冲沸水，随便就好，除非一定需要很高的温度。浸泡时间，可以用白瓷汤匙身进壶中看看汤色，颜色差不多就可以倒出，一般在倒出时就可以看出颜色，颜色不够时，再等一会儿，不过倒出时，壶要稍微摇一摇(浓度可能不均匀)。之后的状况大约与第三泡相同。"},{"text":"置茶量"},{"text":"至于置茶量，因茶叶的不同，置茶量也有差异。这得要试泡几次才会抓到该放多少茶叶。一般的七子饼、茶砖，置茶量约为壶容量的四分之一即可。沱茶，少一点。散茶，很极端的，要自己试试看。如果一两人喝，置茶量可以更少。这样想好象有点“无耻”，所以我讲一下基本原则：置茶量足够，风味呈现较为容易，但是一些瑕疵容易泡出，这时就需要很专心的泡;置茶量少些，泡茶时不需放太多的精神，甚至温度、浸泡时间不要相差太大都可以勉强接受;置茶量不足，风味呈现不易，但是朋友聊天时，最好将置茶量降低一倍左右，聊天聊得忘了倒茶时间也没关系。"}]},{"name":"沱茶功效作用","content":[{"text":"1、常饮沱茶，对年龄40-50岁的人，有明显减肥体重效果。"},{"text":"2、有一半病例，体内胆固醇含量有所下降。对人体降低血尿酸的含量也有一定的作用。"},{"text":"3、无苦味，无涩味，入口爽滑，醇厚，从调理肠胃起到养胃祛寒、减肥降脂、强身健体、延年益寿的效果，老少适益。"},{"text":"4、解油腻牛羊毒，括肠通泄。"},{"text":"5、降低胆固醇的效果则与安妥明相似，且长期饮用无副作用。"},{"text":"6、常饮沱茶，对降低人体所含三酸甘油脂、胆固醇、血尿酸等有不同程度作用。"},{"text":"7、沱茶有杀死癌细胞、抗突变、防癌功能及减肥降血脂作用。"}]}]}